import { Component, OnInit } from '@angular/core';
import {FormGroup,FormControl,Validators} from '@angular/forms'
import {ApiService} from '../Services/Api.service';
import { forEach } from '@angular/router/src/utils/collection';
import {Router} from '@angular/router';
import { Api } from '../Models/Api.model';
import{trigger,state,style,transition,animate,keyframes}from '@angular/animations';

@Component({
  selector: 'app-weather-data',
  templateUrl: './weather-data.component.html',
  styleUrls: ['./weather-data.component.css'],
  animations:[trigger('myAnimation',
  [state('small',style({
    transform:('scale(1)')
  })),
  state('large',style({
    transform:'scale(1.2)',
  })),
  transition('small=>large',animate('300ms ease-in')),

]),
]
})
export class WeatherDataComponent implements OnInit {
  name:string;
  main:string;
  weather:string;
  dt:string;
  sys:string;
  searchWeatherForm:FormGroup;
  state:string="small";
  weathers:Api[];
  constructor(private apiService:ApiService,private myroute:Router) {
    this.searchWeatherForm=new FormGroup({
      word:new FormControl("",[Validators.required])
    });
   }
   get word(){
    return this.searchWeatherForm.get('word');
  }

  ngOnInit() {
  }
  searchWeatherData(searchWeatherForm:FormGroup){
    if(searchWeatherForm.valid){
      return this.apiService.getWeather(searchWeatherForm.value.word)
           .subscribe(data=>this.displayWeather(data),
           error=>this.errorDisplay(error))
    }
 }
 displayWeather(data)
 {
  this.weathers=data;
  this.apiService.storeData(this.weathers);
 }
 animateme()
 {
   this.state=(this.state==='small'?'large':'small');
 }
 errorDisplay(error){
  
     }

}



